import get from 'lodash/get'
import keyBy from 'lodash/keyBy'

import { getBehaviourParams } from '../../keymap'

export function makeIndex (tree) {
  const index = []
  ;(function traverse (tree) {
    const params = tree.params || []
    index.push(tree)
    params.forEach(traverse)
  })(tree)

  return index
}

export function isSimple (normalized) {
  const [first] = normalized.params
  const symbol = get(first, 'source.symbol', get(first, 'source.code', ''))
  const hasIcon = !!get(first, 'source.faIcon')
  const shortSymbol = hasIcon || symbol.length === 1
  const singleParam = normalized.params.length === 1
  return singleParam && shortSymbol
}

export function isComplex (normalized, behaviourParams) {
  const [first] = normalized.params
  const symbol = get(first, 'source.symbol', get(first, 'value', ''))
  const hasIcon = !!get(first, 'source.faIcon')
  const isLongSymbol = !hasIcon && symbol.length > 4
  const isMultiParam = behaviourParams.length > 1
  const isNestedParam = get(first, 'params', []).length > 0

  return isLongSymbol || isMultiParam || isNestedParam
}

export function createPromptMessage (param) {
  const promptMapping = {
    layer: 'Select layer',
    mod: 'Select modifier',
    behaviour: 'Select behaviour',
    command: 'Select command',
    keycode: 'Select key code'
  }

  if (param.name) {
    return `Select ${param.name}`
  }

  return (
    promptMapping[param] ||
    promptMapping.keycode
  )
}

export function hydrateTree (value, params, sources, context = 'none', mergedGlyphs) {
  const bind = value
  const behaviour = get(sources.behavioursByContext[context], bind, {})
  const behaviourParams = getBehaviourParams(params, behaviour)
  const commands = keyBy(behaviour.commands, 'code')

  function getSourceValue (value, as) {
    value = get(sources.defines, value, value)
    if (!Number.isNaN(Number(value))) {
      value = Number(value)
    }
    if (as === 'command') return commands[value]
    if (as.raw || as.enum) return { code: value }
    // TODO: Shouldn't need to render placeholder
    if (as.type === 'placeholder') return { code: value }
    return sources?.[as]?.[value]
  }

  function hydrateNode (node, slot) {
    if (!node) {
      return { value: undefined, slot, params: [] }
    }
    const { value, params, _isPlaceholder } = node
    const source = getSourceValue(value, slot)

    return {
      value,
      _isPlaceholder,
      slot,
      source: {
        ...source,
        symbol: get(mergedGlyphs, [source?.name, 'symbol'], source?.symbol),
        faIcon: get(mergedGlyphs, [source?.name, 'faIcon'], source?.faIcon)
      },
      params: get(source, 'params', []).map((slot, i) => (
        hydrateNode(params[i], slot)
      ))
    }
  }

  return {
    value,
    slot: 'behaviour',
    source: behaviour,
    params: behaviourParams.map((slot, i) => (
      hydrateNode(params[i], slot)
    ))
  }
}
