import PropTypes from 'prop-types'
import { useCallback, useEffect, useRef, useState } from 'react'

import { cycle, scrollIntoViewIfNeeded } from './util'
import style from './style.module.css'

function ResultList ({ results, onSelect, renderResult }) {
  const [cursorIndex, setCursorIndex] = useState(0)
  const listRef = useRef(null)

  const moveCursor = useCallback(function (initial, offset) {
    if (results.length === 0) {
      setCursorIndex(null)
      return
    } else if (offset === undefined) {
      setCursorIndex(initial)
      return
    }

    const next = cursorIndex !== null
      ? cycle(results, cursorIndex, offset)
      : initial

    const selector = `li[data-result-index="${next}"]`
    const element = listRef.current?.querySelector(selector)

    scrollIntoViewIfNeeded(element, false)
    setCursorIndex(next)
  }, [results, cursorIndex, setCursorIndex])

  const handleHighlightNext = useCallback(function () {
    moveCursor(0, 1)
  }, [moveCursor])

  const handleHightightPrev = useCallback(function () {
    moveCursor(results.length - 1, -1)
  }, [moveCursor, results])

  const handleSelectActive = useCallback(function (event) {
    onSelect(results[cursorIndex], event)
  }, [results, cursorIndex, onSelect])

  const handleKeyDown = useCallback(function (event) {
    const mapping = {
      ArrowDown: handleHighlightNext,
      ArrowUp: handleHightightPrev,
      Enter: handleSelectActive
    }

    const action = mapping[event.key]
    if (action) {
      event.preventDefault()
      event.stopPropagation()
      action(event)
    }
  }, [
    handleHighlightNext,
    handleHightightPrev,
    handleSelectActive
  ])

  useEffect(() => {
    document.body.addEventListener('keydown', handleKeyDown)
    return () => document.body.removeEventListener('keydown', handleKeyDown)
  }, [listRef, handleKeyDown])

  return (
    <ul className={style.results} ref={listRef}>
      {results.map((result, i) => (
        <li
          key={i}
          className={cursorIndex === i ? style.highlighted : ''}
          title={result.description}
          data-result-index={i}
          onClick={event => onSelect(result, event)}
          onDoubleClick={event => onSelect(result, event)}
          onMouseOver={() => moveCursor(i)}
        >
          {renderResult(result, i)}
        </li>
      ))}
    </ul>
  )
}

ResultList.propTypes = {
  results: PropTypes.array.isRequired,
  onSelect: PropTypes.func.isRequired,
  renderResult: PropTypes.func.isRequired
}

export default ResultList
