import { useCallback, useMemo } from 'react'

import BindValue from '../../Keyboard/Binding/BindValue'
import styles from '../../Keyboard/Keys/styles.module.css'

const outerStyle = {
  display: 'inline-block',
  cursor: 'pointer',
  verticalAlign: 'text-top',
  border: '0.75px solid var(--key-color-base)',
  borderRadius: '3px'
}
const innerStyle = {
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  width: '10px',
  height: '10px',
  margin: '1px',
  borderRadius: '2px'
}

function HueSatValBindNode ({ node, onSelect }) {
  const { params: [h, s, b] } = node

  const handleSelectParams = useCallback(() => {
    onSelect({ node, type: 'params' })
  }, [node, onSelect])

  const isEmpty = useMemo(() => (
    [h, s, b].some(v => v.value === undefined)
  ), [h, s, b])

  const backgroundColor = `hsl(${h.value}, ${s.value}%, ${b.value}%)`

  return (
    <>
      <BindValue node={node} onSelect={onSelect} />
      {!isEmpty
        ? (
        <div onClick={handleSelectParams} style={outerStyle}>
          <div
            className={isEmpty ? styles.missingValue : null}
            style={{ ...innerStyle, backgroundColor }}
          />
        </div>
          )
        : <BindValue node={{}} onSelect={handleSelectParams} />}
    </>
  )
}

export default HueSatValBindNode
