import PropTypes from 'prop-types'
import { lazy, Suspense, useCallback, useState } from 'react'
import ActionButton from '../../Common/ActionButton'

const ChromePicker = lazy(() => import(/* webpackChunkName: "react-color" */ 'react-color/es/Chrome.js'))

function HSBPicker (props) {
  const { node, onSelect } = props
  const [hsl, setHsl] = useState({
    h: Number(node.params[0].value),
    s: Number(node.params[1].value) / 100,
    l: Number(node.params[2].value) / 100
  })

  const handleChange = useCallback(({ hsl }) => {
    setHsl(hsl)
  }, [setHsl])

  const handleConfirm = useCallback(() => {
    onSelect([
      { value: Math.round(hsl.h) },
      { value: Math.round(hsl.s * 100) },
      { value: Math.round(hsl.l * 100) }
    ])
  }, [hsl, onSelect])

  return (
    <div>
      <Suspense>
        <p>Select colour</p>
        <ChromePicker
          color={hsl}
          disableAlpha={true}
          onChangeComplete={handleChange}
        />
        <p style={{ textAlign: 'center' }}>
          <ActionButton onClick={handleConfirm}>Confirm</ActionButton>
        </p>
      </Suspense>
    </div>
  )
}

HSBPicker.propTypes = {
  value: PropTypes.any.isRequired,
  prompt: PropTypes.string.isRequired,
  param: PropTypes.oneOfType([
    PropTypes.shape({
      enum: PropTypes.array,
      raw: PropTypes.bool,
      type: PropTypes.string
    })
  ]).isRequired,
  onSelect: PropTypes.func.isRequired
}

HSBPicker.defaultProps = {
  value: ''
}

export default HSBPicker
