import compact from 'lodash/compact'
import { useCallback } from 'react'
import PropTypes from 'prop-types'

import * as bindPropTypes from './bindPropTypes'
import styles from '../Keys/styles.module.css'
import Icon from '../../Common/Icon'

export function NullKey () {
  return <span>⦸</span>
}

export function getSourceTitle (source) {
  return source && `(${source.code}) ${source.description}`
}

export function renderSource (source) {
  const text = source && (source?.symbol || source?.code?.toString())
  const icon = source?.faIcon && <Icon name={source.faIcon} />
  return icon || text || <NullKey />
}

function BindValue (props) {
  const { node, onSelect } = props
  const { value, source, _isPlaceholder } = node
  const title = getSourceTitle(source)

  const handleClick = useCallback(function (event) {
    event.stopPropagation()
    onSelect({ node })
  }, [node, onSelect])

  return (
    <span
      className={compact([
        styles.code,
        (value === undefined || _isPlaceholder) && styles.missingValue
      ]).join(' ')}
      title={title}
      onClick={handleClick}
    >
      {renderSource(source)}
    </span>
  )
}

BindValue.propTypes = {
  node: bindPropTypes.bindNode.isRequired,
  onSelect: PropTypes.func.isRequired
}

export default BindValue
