import PropTypes from 'prop-types'
import style from './styles.module.css'

const leftModifiers = ['LSHFT', 'LALT', 'LCTRL', 'LGUI']
const rightModifiers = ['RSHFT', 'RALT', 'RCTRL', 'RGUI']

function ModifiersRow ({ selected, row, onSelect }) {
  return (
    <ul className={style.modifiers}>
      {row.map((mod, i) => (
        <li
          key={i}
          className={selected.includes(mod) ? style.selected : ''}
          onClick={() => onSelect(mod)}
        >{mod}</li>
      ))}
    </ul>
  )
}

function ModifiersPicker ({ selected, onChange }) {
  function handleSelection (modifier) {
    onChange(
      selected.includes(modifier)
        ? selected.filter(mod => mod !== modifier)
        : [...selected, modifier]
    )
  }
  return (
    <>
      <ModifiersRow row={leftModifiers} selected={selected} onSelect={handleSelection} />
      <ModifiersRow row={rightModifiers} selected={selected} onSelect={handleSelection} />
    </>
  )
}

ModifiersPicker.propTypes = {
  selected: PropTypes.arrayOf(PropTypes.string)
}

export default ModifiersPicker
