import PropTypes from 'prop-types'

import KeyWrapper from './KeyWrapper'

import Binding from '../Binding/Binding'
import * as bindPropTypes from '../Binding/bindPropTypes'

function Key (props) {
  const { context, value, params, keyWrapperProps = {}, onUpdate } = props

  return (
    <KeyWrapper {...keyWrapperProps}>
      <Binding
        context={context}
        value={value}
        params={params}
        onUpdate={onUpdate}
      />
    </KeyWrapper>
  )
}

Key.propTypes = {
  label: PropTypes.string,
  value: bindPropTypes.value.isRequired,
  params: PropTypes.arrayOf(bindPropTypes.node),
  onUpdate: PropTypes.func.isRequired
}

export default Key
