import PropTypes from 'prop-types'
import { useContext, useMemo } from 'react'

import { SearchContext } from '../providers'
import ResultList from './ResultList'
import style from './style.module.css'

function EnumPicker (props) {
  const { getSearchTargets } = useContext(SearchContext)
  const { param, prompt, onSelect } = props

  const choices = useMemo(() => (
    getSearchTargets(param)
  ), [getSearchTargets, param])

  return (
    <div className={style.dialog}>
      <p>{prompt}</p>
      <ResultList
        results={choices}
        onSelect={onSelect}
        renderResult={result => result.code}
      />
    </div>
  )
}

EnumPicker.propTypes = {
  value: PropTypes.any.isRequired,
  param: PropTypes.object.isRequired,
  prompt: PropTypes.string.isRequired,
  onSelect: PropTypes.func.isRequired
}

EnumPicker.defaultProps = {
  value: ''
}

export default EnumPicker
