import get from 'lodash/get'
import PropTypes from 'prop-types'
import { useMemo } from 'react'

import * as bindPropTypes from './bindPropTypes'
import BindValue from './BindValue'
import KeycodeBindNode from '../../EnhancedPickers/Keycode/KeycodeBindNode'
import HueSatValBindNode from '../../EnhancedPickers/RGB/HueSatValBindNode'

import styles from '../Keys/styles.module.css'

function GenericBindNode ({ node, onSelect }) {
  return (
    <>
      <BindValue
        node={node}
        onSelect={onSelect}
      />
      {get(node, 'source.params.length') > 0
        ? (
            <BindParamlist
              slots={node.source.params}
              params={node.params}
              onSelect={onSelect}
            />
          )
        : null}
    </>
  )
}

function BindParam (props) {
  const { node, onSelect, slot } = props
  const BindComponent = useMemo(() => {
    // TODO: allow components to register helpers
    if (node.source?.helper === 'hsb-color-picker') return HueSatValBindNode
    if (slot === 'code') return KeycodeBindNode
    return GenericBindNode
  }, [node, slot])

  return (
    <span className={styles.param}>
      <BindComponent node={node} onSelect={onSelect} />
    </span>
  )
}

function BindParamlist (props) {
  const { slots, params, onSelect, root, wrapperProps } = props
  return (
    <span
      className={styles.params}
      data-is-root={!!root}
      data-param-count={params.length}
      {...wrapperProps}
    >
      {slots.map((slot, i) => slot.type !== 'placeholder' && (
        <BindParam
          key={i}
          slot={slot}
          node={params[i]}
          onSelect={onSelect}
        />
      ))}
    </span>
  )
}

BindParamlist.propTypes = {
  slots: PropTypes.arrayOf(bindPropTypes.param).isRequired,
  params: PropTypes.arrayOf(bindPropTypes.bindNode).isRequired,
  source: bindPropTypes.source,
  onSelect: PropTypes.func.isRequired
}

export default BindParamlist
