import PropTypes from 'prop-types'

export const param = PropTypes.oneOfType([
  PropTypes.oneOf(['code', 'layer', 'command']),
  PropTypes.shape({ type: PropTypes.oneOf(['placeholder']) }),
  PropTypes.shape({
    enum: PropTypes.array,
    raw: PropTypes.bool,
    name: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired
  })
])

export const params = PropTypes.arrayOf(param)
export const value = PropTypes.oneOfType([
  PropTypes.string,
  PropTypes.number
])
export const source = PropTypes.shape({
  params,
  code: value.isRequired,
  description: PropTypes.string,
  symbol: PropTypes.string,
  faIcon: PropTypes.string
})

const nodeShape = { value, source }
nodeShape.params = PropTypes.arrayOf(PropTypes.shape(nodeShape))

export const node = PropTypes.shape(nodeShape)

export const bindNode = node

export const index = PropTypes.arrayOf(node)
