
export function validateDevicetreeNodeName (value) {
  if (value.length > 31) {
    return 'maximum length: 31'
  } else if (value.match(/^[^a-zA-Z]/)) {
    return 'first character must be a letter'
  } else if (value.match(/[^a-zA-Z0-9,._+-]/)) {
    return 'valid characters: a-zA-Z0-9,._+-'
  }
}

export const validateDevicetreeIdentifier = validateDevicetreeNodeName

export function validateDevicetreeLabel (value) {
  if (value.length > 31) {
    return 'maximum length: 31'
  } else if (value.match(/^[^a-zA-Z]/)) {
    return 'first character must be a letter'
  } else if (value.match(/[^a-zA-Z0-9_]/)) {
    return 'valid characters: a-zA-Z0-9_'
  }
}

export function validateDevicetreeReference (value) {
  if (value.length > 31) {
    return 'maximum length: 31'
  } else if (value.match(/^[^&]/)) {
    return 'must start with "&"'
  } else if (value.match(/^&[^a-zA-Z]/)) {
    return 'first character must be a letter'
  } else if (value.slice(1).match(/[^a-zA-Z0-9_]/)) {
    return 'valid characters: a-zA-Z0-9_'
  }
}
