import PropTypes from 'prop-types'
import { useMemo } from 'react'
import { useSelector } from 'react-redux'

import { selectLayers } from '../Keyboard/Layers/layersSlice'
import ResultList from './ResultList'
import style from './style.module.css'

function LayerPicker ({ onSelect }) {
  const layers = useSelector(selectLayers)
  const choices = useMemo(() => layers.map((layer, i) => ({
    code: i,
    description: layer.name || `Layer ${i}`
  })), [layers])

  return (
    <div className={style.dialog}>
      <p>Select layer</p>
      <ResultList
        results={choices}
        onSelect={onSelect}
        renderResult={result => (
          <>
            {result.code}
            {result.description && <p className={style.description}>{result.description}</p>}
          </>
        )}
      />
    </div>
  )
}

LayerPicker.propTypes = {
  value: PropTypes.string.isRequired,
  behaviour: PropTypes.object.isRequired,
  onSelect: PropTypes.func.isRequired
}

LayerPicker.defaultProps = {
  value: ''
}

export default LayerPicker
