import fuzzysort from 'fuzzysort'
import PropTypes from 'prop-types'
import { useCallback, useContext, useMemo } from 'react'
import { bindNode } from '../Keyboard/Binding/bindPropTypes'

import { SearchContext } from '../providers'
import ResultList from './ResultList'
import SearchFilter from './SearchFilter'
import style from './style.module.css'

function GenericPicker (props) {
  const { prompt, searchKey, node, param, behaviour, context, onSelect } = props

  const { getSearchTargets } = useContext(SearchContext)
  const renderSearchResult = useCallback(result => {
    const title = result.search
      ? (
      <span dangerouslySetInnerHTML={{
        __html: fuzzysort.highlight(result.search)
      }} />
        )
      : (
      <span>
        {result.code}
      </span>
        )

    return (
      <>
        {title}
        {result.description && <p className={style.description}>{result.description}</p>}
      </>
    )
  }, [])

  const choices = useMemo(() => {
    return getSearchTargets(param, behaviour.code, context)
  }, [getSearchTargets, param, behaviour, context])

  return (
    <div className={style.dialog}>
      <p>{prompt}</p>
      <SearchFilter
        choices={choices}
        initialQuery={node.value || ''}
        searchKey={searchKey}
        renderResults={results => results && (
          <ResultList
            results={results}
            onSelect={onSelect}
            renderResult={renderSearchResult}
          />
        )}
      />
    </div>
  )
}

GenericPicker.propTypes = {
  node: bindNode.isRequired,
  param: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.shape({
      enum: PropTypes.array,
      raw: PropTypes.bool,
      type: PropTypes.string
    })
  ]).isRequired,
  prompt: PropTypes.string.isRequired,
  searchKey: PropTypes.string.isRequired,
  behaviour: PropTypes.object.isRequired,
  context: PropTypes.string.isRequired,
  onSelect: PropTypes.func.isRequired
}

export default GenericPicker
