// `code` values may be "trees" in terms of structure, but this is only
// because of "modifier macros" (e.g. `LS(A)`) which may be nested recursively
// but each level of the tree has at most one child. This means the deepest
// node is our actual keycode and everything above that is a modifier.
export function treeToSequence (tree) {
  return tree.params.length > 0
    ? [tree, ...treeToSequence(tree.params[0])]
    : [tree]
}

export function sequenceToTree ([value, ...seq]) {
  const params = seq.length === 0 ? [] : [sequenceToTree(seq)]
  return { value, params }
}
